import * as React from "react";
import { useSelector } from 'react-redux';

//import { selectTempData } from "../features/temp/tempSlice";
import { selectCaseArray, selectCaseLayoutStatusById, selectCasePeriods } from "../features/case/caseSlice"

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

import processRecords from "../../../utilities/processRecords"


const balanceIndicators = [

    { id: 1, title: "", type: 'asset' },
    { id: 2, title: "", type: 'asset' },
    { id: 3, title: " ", type: 'asset' },
    { id: 4, title: " ", type: 'asset' },
    { id: 5, title: " ", type: 'asset' },
    { id: 6, title: "", type: 'asset' },

    { id: 7, title: " ", type: 'asset' },


    { id: 8, title: "", type: "liability" },

    { id: 9, title: " ", type: 'liability' },
    { id: 10, title: "  ", type: 'liability' },
    { id: 11, title: "  ", type: 'liability' },
    { id: 12, title: " ", type: 'equity' },

    { id: 13, title: "", type: 'equity' },

    { id: 14, title: " ", type: 'equity' }
]


export default function ShowBalance() {
    // const records = useSelector(selectTempData).dataArray;
    const status = useSelector(selectCaseLayoutStatusById("showbalance"));
    const records = useSelector(selectCaseArray); //.slice(0, 2)
   // const periods = useSelector(selectCasePeriods);

    let periods = [...new Set(records.map(item => item.period))];

    //     let series = [];
    // balanceIndicators.forEach(element => {
    //   console.log(element);
    //     if (element.title.length > 2) {
    //         series.push({
    //             name: element.title,
    //             data: processBalanceIndicatorForStackedBarsChart(element.title, records, periods, balanceIndicators)
    //         })
    //     }
    // });

    //    console.log(series);

    if (!status) { return null }

    return <Container className="mt-5 animated fadeIn">
        <Row className="bg-secondary text-white">
            {["", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {period}
                </Col>
            ))}
        </Row>
        <Row>
            {[" ", ...periods].map((period, index) => (
                <Col className="font-weight-bold" key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? " "
                        : processRecords( //processRecords(" ", period)}
                            {
                                indicator: " ",
                                balanceIndicators: balanceIndicators,
                                records: records,
                                periods,
                                period
                            }
                        )
                    }
                </Col>
            ))}
        </Row>
        <Row>
            {["", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0 ? ""  ////    processRecords("", period)}
                        : processRecords(
                            {
                                indicator: "",
                                balanceIndicators: balanceIndicators,
                                records: records,
                                periods,
                                period
                            }
                        )}


                </Col>
            ))}
        </Row>
        <Row>
            {[" ", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? " "
                        : processRecords({
                            indicator: " ",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period
                        })}
                </Col>
            ))}
        </Row>
        <Row>
            {[" ", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? " "
                        : processRecords({
                            indicator: " ",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period
                        })}
                </Col>
            ))}
        </Row>

        <Row>
            {[" ", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? " " //    : processRecords(" ", period)}
                        : processRecords({
                            indicator: " ",
                            balanceIndicators,
                            records: records,
                            periods,
                            period
                        })}

                </Col>
            ))}
        </Row>
        <Row>
            {["", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0 ? "" :
                     processRecords({indicator: "",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period})}
                </Col>
            ))}
        </Row>
        <Row className="bg-secondary text-white">
            {["", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {period}
                </Col>
            ))}
        </Row>
        <Row>
            {[" ", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? " " //processRecords(" ", period)}
                        : processRecords(
                            {
                                indicator: " ",
                                balanceIndicators,
                                records: records,
                                periods,
                                period
                            }
                        )}
                </Col>
            ))}
        </Row>
        <Row>
            {[" ", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? " "
                        : processRecords({indicator: " ",
                        balanceIndicators: balanceIndicators,
                        records: records,
                        periods,
                        period})}
                </Col>
            ))}
        </Row>
        <Row className="bg-light text-dark">
            {["  ", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? "  "
                        : processRecords({indicator: "  ",
                        balanceIndicators: balanceIndicators,
                        records: records,
                        periods,
                        period})}
                </Col>
            ))}
        </Row>
        <Row className="bg-light text-dark">
            {["  ", ...periods].map(
                (period, index) => (
                    <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                        {index === 0
                            ? "  "
                            : processRecords({indicator:  "  ",
                            balanceIndicators: balanceIndicators,
                            records: records,
                            periods,
                            period})}
                    </Col>
                )
            )}
        </Row>
        <Row className="bg-light text-dark">
            {[" ", ...periods].map((period, index) => (
                <Col key={period} xs={index > 0 ? 1 : 12 - periods.length}>
                    {index === 0
                        ? " "
                        : processRecords({indicator:  " ",
                        balanceIndicators: balanceIndicators,
                        records: records,
                        periods,
                        period})}
                </Col>
            ))}
        </Row>
    </Container>

}